@prefixClass: rc-progress;

.@{prefixClass} {
  &-circle-main {
    line-height: 1.5;
    position: relative;
  }

  &-circle-text {
    position: absolute;
    top: 50%;
    left: 0;
    right: 0;
    margin-top: -0.75em;
    text-align: center;
    font-size: 100px;
    letter-spacing: 0;
  }

}


